.suggestion {
  background-color: #fff6f7;
  border: 1px solid #ada9a8;
  display: flex;
  flex-flow: row wrap;
  justify-content: flex-start;
  align-items: flex-start;
  padding: 5px 16px 16px;

  ul {
    width: 100%;
    list-style-type: none;
    margin: 0;
    padding: 0;

    li {
      display: inline-block;
      cursor: pointer;
      flex-grow: 0;
      flex-shrink: 0;
      font-size: 13px;
      margin: 5px;
      color: #fff;
      border-radius: 3px;
      white-space: nowrap;

      a {
        background: #f63577;
        line-height: 31px;
        padding: 7px 10px;
      }
    }
  }
}

.suggestion-list {

  p {
    font-size: 15px;
    text-align: left;
  }

  span {
    border: 1px solid #f63577;
    float: left;
    display: inline;
    list-style: none;
    color: #f63577;
    background-color: #fff;
    margin-left: 10px;
    padding: 3px;
    margin-bottom: 5px
  }

  span:hover {
    background-color: #fff0f5;
    transition: .3s;
  }
}